@use "./scss/" as *;

@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600;700;800;900&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: $font-family;
}

html{
    font-size: 100%;
    @include tablet{
        font-size: 80%;
    }
    @include mobile{
        font-size: 60%;
    }
}

body{
    font-weight: 400;
    line-height: 1.5;
    padding-top: $header-height;

    background-color: $body-bg;
    color: $txt-color;

    @include mobile{
        margin-bottom: 3rem;
    }
}

a{
    text-decoration: none;
    color: unset;
}

@media (hover:hover){
    a:hover{
        color: $main-color;
    }
}

button, input{
    outline: 0;
}

ul{
    list-style-type: none;
}

img{
    max-width: 100%;
}

iframe{
    border:0;
}

.container{
    max-width: 1660px;
    margin: auto;
}

.section{
    padding: 5rem;
}
